import { styled } from "@mui/system";
import { Box, Typography, Grid, Hidden} from "@mui/material";
import useBlogList from '../CustomHooks/useBlogList';
import BlogCard from './BlogCard';
import useMediaQuery from '@mui/material/useMediaQuery';
import {useHistory} from 'react-router-dom';
import SpiritButton from '../SpiritButton';
import {useTranslation} from 'react-i18next';




const Blog = () => {

    const {blogList} = useBlogList();
    const matches = useMediaQuery('(min-width:900px)');
    const history = useHistory();
    const {t} = useTranslation();


    return (
        <Grid container>
            <Grid item container direction="column" alignItems="center" mt="200px">
                <Typography variant="h3" textAlign="center" fontSize={matches ? "50px" : "28px"}>{t("Latest Blogs")}</Typography>
                <Typography variant="body1" textAlign="center" fontSize={matches ? "25px" : "16px"} mt="24px">{t("Latest blogs in the world of elegancy")}</Typography>
            </Grid>
            <Grid item>
                {blogList && blogList.map((item, i) => {
                    if(i > 1) return null;
                    if(i % 2 === 0) return <BlogCard data={item} key={item.id} />
                    return <BlogCard data={item} leftImg key={item.id} />
                })}
            </Grid>
            <Grid item container justifyContent="center" mt='70px'>
                <SpiritButton mobile={!matches} onClick={() => history.push('/blog')}>{t("View All")}</SpiritButton>
            </Grid>
        </Grid>
    )
}

export default Blog;