// `


// max-width: 311px;
// max-height: 466px;
// width: 100%;
// height: 100%;

const BackPhotoWrapper = styled('div')`
    grid-row: 1;
    grid-column: 1 / span 8;
`

// max-width: 300px;
// max-height: 444px;
// width: 100%;
// height: 100%;

const FrontPhotoWrapper = styled('div')`
    grid-row: 1;
    grid-column: 6 / span 7;
    margin-top: 70%;
    z-index: 1;
`

const PhotoWrapper2 = styled('div')`
    max-width: 599px;
    max-height: 888px;
    display: grid;
    position: relative;
    grid-template-columns: repeat(12, 1fr);
    align-content: center;
`



const Line = styled('span')`
    width: 100%;
    border-top: solid 2px #0f0f0f;
    display: block;
    margin-top: 23px;
    margin-right: 5px;
`

const Image = styled('img')`
    width: 100%;
    display: block;
`



const HeightWrapper = styled('div')`
    min-hegiht: 770px;
`



const CommonBanner = ({data}) => {
    const {t} = useTranslation();
    const {variant, type, header, subHeader, content, images} = data;
    const history = useHistory();


    const handleClick = (event) => {
        const target = event.target.dataset.link;
        // console.log(target)
        history.push('/shop', { selectedType: target })
    } 

    return (
      <HeightWrapper>
        <Hidden mdDown>
          <Grid container>
            <Grid 
                item 
                container 
                xs={6} 
                justifyContent="center"
                sx={{
                    order: variant === 'left' ? 1 : 2,
                }}
            >
              <PhotoWrapper2>
                <BackPhotoWrapper>
                  <Image src={images[0]} />
                </BackPhotoWrapper>
                <FrontPhotoWrapper>
                  <Image
                    src={images[1]}
                    style={{ border: "solid 10px #f2f2f2" }}
                  />
                </FrontPhotoWrapper>
              </PhotoWrapper2>
            </Grid>
            <Grid 
                item 
                container xs={6} 
                flexDirection="column" 
                p={variant === 'left' ? "0 0 0 25px" : "0 25px 0 25px"}
                sx={{
                    order: variant === 'left' ? 2 : 1,
                }}
            >
              <Grid item>
                <Typography variant="h2" mt="138px">
                  {t(header)}
                </Typography>
              </Grid>
              <Grid item>
                <Typography variant="h3" mt="30px">
                  {t(subHeader)}
                </Typography>
              </Grid>
              <Grid container item mt="60px">
                <Grid item xs={1}>
                  <Line />
                </Grid>
                <Grid item xs={10}>
                  <Grid item xs={12}>
                    <Typography
                      variant="body2"
                      fontSize="20px"
                      sx={{
                        display: "flex",
                        paddingTop: "10px",
                        paddingLeft: "10px",
                        lineHeight: "35px",
                      }}
                    >
                      {content}
                    </Typography>
                  </Grid>
                  <Grid item xs={12} pt="53px" pl="10px">
                    <SpiritButton onClick={handleClick} data-link={type}>{t("View All")}</SpiritButton>
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </Hidden>
        <Hidden mdUp>
          <Grid container>
            <Grid item container flexDirection="column" alignItems="center">
              <Grid item>
                <Typography variant="h2" fontSize="40px" mt="138px" textAlign="center">
                  {t(header)}
                </Typography>
              </Grid>
              <Grid item>
                <Typography variant="h3" fontSize="25px" mt="16px" textAlign="center">
                  {t(subHeader)}
                </Typography>
              </Grid>
              <Grid item container>
                <Grid
                  item
                  container
                  xs={1}
                  alignContent="center"
                  justifyContent="center"
                >
                  <Line style={{ maxHeight: "2px", maxWidth: "34px" }} />
                </Grid>
                <Grid item xs={10}>
                  <Typography
                    variant="body2"
                    fontSize="16px"
                    sx={{
                      lineHeight: "35px",
                      padding: "15px",
                    }}
                    textAlign="center"
                  >
                    {content}
                  </Typography>
                </Grid>
                <Grid
                  item
                  container
                  xs={1}
                  alignContent="center"
                  justifyContent="center"
                >
                  <Line style={{ maxHeight: "2px", maxWidth: "34px" }} />
                </Grid>
              </Grid>
            </Grid>
            <Grid item container mt="30px" justifyContent="center">
              <PhotoWrapper2>
                <BackPhotoWrapper>
                  <Image src={images[0]} />
                </BackPhotoWrapper>
                <FrontPhotoWrapper>
                  <Image
                    src={images[1]}
                    style={{ border: "solid 10px #f2f2f2" }}
                  />
                </FrontPhotoWrapper>
              </PhotoWrapper2>
            </Grid>
            <Grid item container mt="135px" justifyContent="center">
              <SpiritButton mobile onClick={handleClick} data-link={type}>{t("View All")}</SpiritButton>
            </Grid>
          </Grid>
        </Hidden>
      </HeightWrapper>
    );
}

export default withCommonBannerWrapper(CommonBanner);