import Navbar from '../navbar/Navbar';
import Banner from '../banner/Banner';
import { styled } from "@mui/system";
import SpiritWineItem from '../carousel/SpiritWineItem';
import Carousel from '../carousel/Carousel';
import CommonBanner from '../banner/CommonBanner';
import Blog from "../blog/Blog";
import Brands from "../brands/Brands";
import Footer from "../footer/Footer";

const PageWrapper = styled('div')`
  max-width: 1770px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
`
const BannerWrapper = styled('div')`
  // background-image: url("images/banner.jpg");
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("images/banner.jpg");
  background-repeat: no-repeat;
  height: 981px;
  background-size:100% 100%;
  width: 100%;
  display: inline-block;
`

const CommonBannerWrapper = styled('div')`
  background-image: url("images/abstract-lines-transparent-png.png");
  background-repeat: no-repeat;
  background-size:100% 100%;
  padding-bottom: 50px;
  width: 100%;
  display: inline-block;
  margin-top: 233px;
`

const FooterWrapper = styled('div')`
  width: 100%;
  background-color: #202020;
  height: 100%;
  margin-top: 205px;
  color: #fbfbfb;
`

const dummyBannerWineData = {
  variant: 'left',
  type: 'Wine', 
  header: 'Modern Wine', 
  subHeader: 'Best Wine from Georgia',
  images: ['images/big-photo-2.jpg', 'images/small-photo-1.jpg'], 
  content: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam purus quam. Lorem ipsum dolor sit ame. Aliquam purus quam. Lorem ipsum dolor sit ame.'
}

const dummyBannerChachaData = {
  variant: 'right',
  type: 'Chacha', 
  header: 'Sweetest Chacha', 
  subHeader: 'Best Chacha from Georgia',
  images: ['images/chacha-1.jpg', 'images/chacha-2.jpg'], 
  content: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam purus quam. Lorem ipsum dolor sit ame. Aliquam purus quam. Lorem ipsum dolor sit ame.'
}

function MainPage() {
    return (
      <>
        <Navbar white />
        <Navbar />
        <div style={{backgroundColor: '#f5f5f5'}}>
          <BannerWrapper>
            <PageWrapper>
              <Banner />
            </PageWrapper>
          </BannerWrapper>
          <PageWrapper>
            <Carousel />
          </PageWrapper>
          <CommonBanner data={dummyBannerWineData} />
          <PageWrapper>
            <Carousel />
          </PageWrapper>
          <CommonBanner data={dummyBannerChachaData} />
          <PageWrapper>
            <Blog />
          </PageWrapper>
          <PageWrapper>
            <Brands />
          </PageWrapper>
          <FooterWrapper>
            <PageWrapper>
              <Footer />
            </PageWrapper>
          </FooterWrapper>
        </div>
      </>
    );
  }
  
  export default MainPage;
  
  
  