import React from 'react';
import { styled } from "@mui/system";

const CommonBannerWrapper = styled('div')`
  padding-bottom: 50px;
  width: 100%;
  display: inline-block;
  margin-top: 233px;
  position: relative;
  &:after {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-image: url("images/abstract-lines-transparent-png.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    z-index: 1;
    opacity: 0.15;
  }
`

const PageWrapper = styled('div')`
  max-width: 1770px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
  position: relative;
  z-index: 2;
`



const withCommonBannerWrapper = (Component) => ({ ...props }) => (
    <CommonBannerWrapper sx={{'&:after': {transform: props.data.variant === 'right' ? 'rotate(180deg)' : 'none'}}}>
        <PageWrapper>
            <Component {...props} />
        </PageWrapper>
    </CommonBannerWrapper>
  );

export default withCommonBannerWrapper;