import { MenuIcon } from '../../../spiritIcons';
import { Drawer, Typography, Grid, Popover} from '@mui/material/';
import * as React from 'react';
import { styled } from "@mui/system";
import { PhoneIcon, EmailIcon, LocationIcon, FacebookIcon, TelegramIcon, InstagramIcon } from '../../../spiritIcons'
import { useHistory } from "react-router-dom";
import SearchBar from '../SearchBar';
import {useTranslation} from "react-i18next";


const LangButton = styled(Typography)`
        cursor: pointer;
        user-select: none;
    `



const HamburgerMobile = ({ white, handleModal, ...props }) => {
    const [state, setState] = React.useState(false);
    const {t, i18n} = useTranslation();


    const isLoggedIn = sessionStorage.getItem('logged_in')
    const history = useHistory();

    const toggleDrawer = (toggle) => {
        console.log("hello");
        setState(toggle);
    };


    const StyledItems = styled('div')`
        border-bottom: 1px solid #202020;
        cursor: pointer;
        height: 30px;
    `

    const handleItemClick = (e) => {
        const item = e.currentTarget.getAttribute('value');
        switch(item){
            case 'home':
                history.push('/');
                break;
            case 'shop':
                history.push('/shop');
                break;
            case 'blog':
                history.push('/');
                break;
            case 'profile':
                history.push('/profile');
                break;
            case 'login':
                handleModal(true);
                setState(false);
                break;
            default:
                history.push('/');
                break;
        }
    } 



    return (
        <>
            <MenuIcon white={white ? true : false } onClick={() => toggleDrawer(true)} sx={{ cursor: 'pointer' }} {...props} />
            <Drawer
                anchor="top"
                open={state}
                onClose={() => toggleDrawer(false)}
                elevation={0}
                sx={{
                    '.MuiDrawer-paperAnchorTop': {
                        top: '57px',
                        paddingLeft: '15px',
                        paddingRight: '15px'
                    }
                }}
            >
                <StyledItems>
                    <Typography value="home" variant="body1" component="div" fontSize="16px" lineHeight={2} onClick={handleItemClick}><b>{t("Home")}</b></Typography>
                </StyledItems>
                <StyledItems>
                    <Typography value="shop" variant="body1" component="div" fontSize="16px" lineHeight={2} onClick={handleItemClick}><b>{t("Shop")}</b></Typography>
                </StyledItems>
                <StyledItems>
                    <Typography value="blog" variant="body1" component="div" fontSize="16px" lineHeight={2} onClick={handleItemClick}><b>{t("Blog")}</b></Typography>
                </StyledItems>
                {
                    isLoggedIn
                    ?
                    <StyledItems>
                        <Typography value="profile" variant="body1" component="div" fontSize="16px" lineHeight={2} onClick={handleItemClick}><b>{t("Profile")}</b></Typography>
                    </StyledItems>
                    :
                    <StyledItems>
                        <Typography value="login" variant="body1" component="div" fontSize="16px" lineHeight={2} onClick={handleItemClick}><b>{t("Log In")}</b></Typography>
                    </StyledItems>
                }
                <Grid container item pt="15px">
                        <LangButton   LangButton component="div" variant="body2" fontSize="16px"
                        sx={{opacity: i18n.language === 'en' ? '1'  : '0.2'}} 
                        onClick={() => i18n.changeLanguage('en')}><b>EN</b></LangButton>
                        <LangButton component="div" variant="body2" fontSize="16px" sx={{
                            marginLeft: '7px',
                            opacity: i18n.language === 'ge' ? '1'  : '0.2'
                        }} onClick={() => i18n.changeLanguage('ge')}><b>GEO</b></LangButton>
                </Grid>
                <SearchBar mobile />
            </Drawer>
        </>
    )
}

export default HamburgerMobile;